<template>
    <div class="sail" data-enable="true">
        <!-- 助航信息 -->
        <div class="sail-info panel">
            <mu-ripple class="por flex-1 flex aic jcc h" tag="span" @click="toggleVelocityUnit">
                速度：
                <span>
                    <span>{{speed}}</span>
                    {{ship.unit}}
                </span>
            </mu-ripple>
            <i class="line h33"></i>
            <span class="flex-1 flex aic jcc h">
                极坐标：
                <span>
                    <span>{{info.polar}}</span>
                    度
                </span>
            </span>
        </div>
        <!-- 助航信息 -->
        
        <!-- 语音&文字按钮 -->
        <speech-button class="map-button shadow" v-model="speechStatus" @input="handlerSpeech"/>
        <word-button class="map-button shadow" v-model="wordStatus"/>
        <!--<speech-gender-button class="map-button shadow speech-gender-button" v-model="wordStatus"/>-->
        <!-- 语音&文字按钮 -->
        
        <!-- 提示 -->
        <div class="sail-tip">{{tips}}</div>
        <!-- 提示 -->
        
        <!-- 消息提示 -->
        <sailor-message :top="70" :left="60" :right="60" ref="message"/>
        <!-- 文字展示 -->
        
        <!-- 助航按钮 -->
        <transition name="fade" mode="out-in">
            <div class="sail-control panel shadow" v-if="!firstClick">
                <mu-button class="button" color="#F5F5F5" @click="firstClick = true" flat>退出</mu-button>
                <div class="distance border-1px-l border-1px-r">
                    剩余 <span>{{info.distance | formatDistance}}</span>
                    {{Number.parseFloat(info.distance) > 1000 ? '公里' : '米'}}
                </div>
                <mu-button class="button" color="#F5F5F5" @click="handlerToggle(courseStatus)" flat>
                    {{courseStatus ? '正北' : '航向'}}
                </mu-button>
            </div>
            <div class="slider panel shadow" v-else>
                <mu-button class="button" color="#F5F5F5" @click="firstClick = false" flat>取消</mu-button>
                <mu-ripple class="slider-inner border-1px-l"
                           @click="retreat"
                           ref="slider">
                    退出助航
                </mu-ripple>
            </div>
        </transition>
        <!-- 助航按钮 -->
    </div>
</template>

<script>
    import {routeMixins, appMixins} from "../../common/mixins"
    import SpeechButton from '../../components/map/speech-button/speech-button'
    import WordButton from '../../components/map/word-button/word-button'
    import SpeechGenderButton from '../../components/map/sail-speech-gender-button/sail-speech-gender-button'
    import SailorMessage from '../../components/sailor/message/message'
    
    import {tips} from "../../types/tips"
    
    const KN = '节'
    const KM = 'km/h'
    
    export default {
        name: "sail",
        mixins: [routeMixins, appMixins],
        data() {
            return {
                info: {
                    speed: 0,
                    polar: 0,
                    distance: 0
                },
                word: '',
                speechStatus: true,
                wordStatus: true,
                courseStatus: true,
                ship: {
                    id: '',
                    speed: 0,
                    draft: 0,
                    pureHeight: 0,
                    unit: KN
                },
                // 动画
                firstClick: false,
                tips: tips.map_tips.navigator
            }
        },
        computed: {
            speed() {
                return Math.round(this.ship.unit === KM ? this.info.speed : (this.info.speed / 1.852))
            }
        },
        created() {
            window.$types.map.navigation.setData = this.setData
            window.$types.map.navigation.setWord = this.setWord
            window.$types.map.navigation.message = this.handlerMessage
            window.$types.map.navigation.message.close = this.deleteMessage
            window.$types.map.navigation.close = this.close
        },
        activated() {
            this.firstClick = false
            const params = this.$route.params
            
            if (JSON.stringify(params) !== '{}') {
                this.info.speed = params.speed
                this.info.polar = params.polar
                this.info.distance = params.distance
                this.word = params.word
                this.ship = params.ship
            }
            
            this.courseStatus = true
            
            this.speechStatus = this.speech
            this.wordStatus = this.message
            
            this.opacity = 0
        },
        methods: {
            toggleVelocityUnit() {
                this.ship.unit = this.ship.unit === KM ? KN : KM
            },
            /***
             * 退出助航
             */
            retreat() {
                this.resetRoutes()
                
                window.Arcgis && window.Arcgis.cancelPathPlanning('')
                
                this.$router.replace('/')
            },
            /***
             * 修改助航数据
             * @param info  JSON String
             */
            setData(info) {
                try {
                    info = JSON.parse(info)
                } catch (e) {
                    console.log(e)
                    info = {
                        speed: 0,
                        polar: 0,
                        distance: 0
                    }
                }
                
                this.info = info
            },
            /***
             * 助航结束
             */
            close(data) {
                try {
                    data = JSON.parse(data)
                } catch (err) {
                    console.log(err)
                    
                    data = {}
                }
                
                this.$router.replace({
                    name: 'sail-over',
                    query: {data, ship: this.ship}
                })
            },
            /***
             * 助航状态下切换视角
             * @param value
             */
            handlerToggle(value) {
                const type = value ? 'trueNorth' : 'course'
                
                window.Arcgis && window.Arcgis.changeNavigationType(type, () => {
                    this.courseStatus = !value
                })
            },
            /***
             * 开关助航语音
             */
            handlerSpeech(value) {
                if (value) {
                    window.Arcgis && window.Arcgis.resumeBroadcast()
                } else {
                    window.Arcgis && window.Arcgis.pauseBroadcast()
                }
            },
            setWord(data) {
                this.handlerMessage({
                    type: 'text',
                    message: data
                })
            },
            handlerMessage(message = {}) {
                message.id = message.id ? message.id : `sailor-message_${Date.now()}`
                
                this.$refs.message.handlerMessage(message)
            },
            deleteMessage(id) {
                this.$refs.message.deleteMessage(id)
            }
        },
        components: {
            SpeechButton, WordButton, SpeechGenderButton,
            SailorMessage
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/mixins.styl"
    
    .sail
        position absolute
        top 0
        left 0
        right 0
        bottom 0
        background-color: transparent !important
        overflow hidden
        
        .panel
            position absolute
            left 0
            right 0
            z-index 100
            display flex
            align-items center
            margin 10px 13px
            height 50px
            background-color rgba(0, 0, 0, .33)
            border-radius 6px
            overflow hidden
            transition all 0.3s
        
        .sail-info
            top 0
            
            > span
                flex 1
                font-size 14px
                text-align center
                color #F5F5F5
                
                > span
                    color #F5F5F5
                    
                    > span
                        font-size 1.25em
            
            .line:before
                left-line(#f5f5f5)
        
        .slider
            bottom 0
            color #fff
            
            .button
                height 100%
            
            .slider-inner
                flex 1
                display flex
                align-items center
                justify-content center
                height 100%
                color #fff
                background-color transparent
        
        .sail-control
            bottom 0
            
            .distance
                flex 1
                text-align center
                color #F5F5F5
                
                > span
                    font-size 1.5em
            
            .button
                height 100%
        
        .speech-button
            position absolute
            top 110px
            left 13px
        
        .word-button
            position absolute
            top 70px
            left 13px
        
        .sail-tip
            position absolute
            top 50%
            right 13px
            padding 10px 8px
            width 30px
            line-height 1.8
            font-size 14px
            text-align center
            color #fff
            background-color rgba(0, 0, 0, .33)
            border-radius 5px
            transform translate3d(0, -50%, 0)
        
        .speech-gender-button
            position absolute
            top 150px
            left 13px
        
        .word-wrapper
            position absolute
            top 70px
            left 53px
            right 13px
            line-height 32px
            min-height 32px
            max-height 90px
            border-radius 5px
            opacity 0.9
            overflow-x hidden
            overflow-y auto
            
            .words
                padding 0 13px
                min-height 32px
                line-height 32px
                text-align justify
                transition all .5s
</style>



