<template>
    <mu-ripple class="speech-button" @click="click">
        <mu-icon :class="{'speech-button__status': value}" value="record_voice_over"/>
    </mu-ripple>
</template>

<script>
    export default {
        name: "map-speech-button",
        props: {
            value: {}
        },
        methods: {
            click() {
                this.$emit('input', !this.value)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .speech-button
        width 32px
        height 32px
        
        .speech-button__status
            color #2e7bff
</style>



