<template>
    <div class="message-item" :class="{'message-item__images': type === 'image'}">
        <div class="message-item__images_container" v-if="type === 'image'">
            <div class="message-item__image_container"
                 v-for="image in images"
                 :style="{width: `${100 / images.length}%`}">
                <div class="message-item__image_inner" :style="{backgroundImage: `url('${icons[image]}')`}"></div>
            </div>
        </div>
        <slot>{{message}}</slot>
    </div>
</template>

<script>
    export default {
        name: "message-item",
        data() {
            return {
                icons: {
                    import: require('../../../../static/image/sailor/import.png'),
                    export: require('../../../../static/image/sailor/export.png'),
                    iae: require('../../../../static/image/sailor/impot_and_export.png')
                }
            }
        },
        props: {
            type: {
                required: true,
                type: String,
                default: 'text'
            },
            message: {
                type: String,
                default: ''
            },
            images: {
                type: Array,
                default: () => ([])
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .message-item
        display flex
        justify-content center
        flex-direction column
        padding 0 10px
        min-height 32px
        line-height 1.74625
        border-radius 5px
        background-color rgba(255, 255, 255, .8)
        transition all .5s
        
        &.message-item__images
            padding 10px
            background-color #2d3a4b
            color #FFF
            text-align center
        
        & + &
            margin-top 10px
            
        .message-item__images_container
            position relative
            display flex
            margin-bottom 5px
            padding 0 2%
            width 100%
            
            &:before, &:after
                content ''
                position absolute
                top 0
                background url("../../../../static/image/sailor/impot_and_export.png") no-repeat
                background-size auto 100%
            
            &:before
                left 1px
                width 2%
                height 100%
                background-position 100% center
            
            &:after
                right 1px
                width 2%
                height 100%
                background-position 0 center
                
            .message-item__image_container
                position relative
                
                .message-item__image_inner
                    padding-top 100%
                    height 0
                    background-color transparent
                    background-repeat no-repeat
                    background-position center
                    background-size 102% 100%

</style>



