<template>
    <mu-ripple class="word-button" @click="click">
        <mu-icon :class="{'map-word-button__status': value}" value="translate"/>
    </mu-ripple>
</template>

<script>
    export default {
        name: "map-word-button",
        props: {
            value: {}
        },
        methods: {
            click() {
                this.$emit('input', !this.value)
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .word-button
        width 32px
        height 32px
        
        .map-word-button__status
            color #2e7bff
</style>



