<template>
    <transition-group tag="div" name="sailor-message" class="message-wrapper" :style="styles">
        <message-item v-for="(item, index) in list" :key="item.id"
                      :type="item.type"
                      :images="item.images">
            {{item.message}}
        </message-item>
    </transition-group>
</template>

<script>
    import MessageItem from './message-item'
    
    export default {
        name: "sailor-message",
        data() {
            return {
                list: []
            }
        },
        props: {
            top: {
                type: Number,
                default: undefined
            },
            left: {
                type: Number,
                default: undefined
            },
            right: {
                type: Number,
                default: undefined
            },
            bottom: {
                type: Number,
                default: undefined
            }
        },
        computed: {
            styles() {
                let top, left, right, bottom
                
                if (this.top) top = this.top + 'px'
                if (this.left) left = this.left + 'px'
                if (this.right) right = this.right + 'px'
                if (this.bottom) bottom = this.bottom + 'px'
                
                return {top, left, right, bottom}
            }
        },
        methods: {
            handlerMessage(message = {}) {
                this.list.unshift(message)
                
                setTimeout(() => {
                    this.list.splice(this.list.findIndex(item => item.id === message.id), 1)
                }, 2000)
            },
            deleteMessage(id) {
                if (id) {
                    this.list.splice(this.list.findIndex(item => item.id === id), 1)
                } else {
                    this.list = []
                }
            }
        },
        components: {
            MessageItem
        }
    }
</script>

<style lang="stylus" scoped>
    .message-wrapper
        position absolute
    
    .sailor-message-enter, .sailor-message-leave-to {
        opacity 0
        transform translate3d(0, 30px, 0)
    }
    
    .sailor-message-leave-active {
        position absolute
    }
</style>



